import { wPage } from '@morjs/core';
import { formatTime } from '../../utils/util';
wPage({
    data: {
        logs: []
    },
    onLoad: function () {
        this.setData({
            logs: (wx.getStorageSync('logs') || []).map(function (log) {
                return {
                    date: formatTime(new Date(log)),
                    timeStamp: log
                };
            })
        });
    }
});
